/* font-family: "OpenSansRegular"; */
@font-face {
	font-family: "LatoRegular";
	src: url("../fonts/LatoRegular/LatoRegular.eot?#iefix")format("embedded-opentype"),
		url("../fonts/LatoRegular/LatoRegular.woff") format("woff"),
		url("../fonts/LatoRegular/LatoRegular.woff2") format("woff2"),
		url("../fonts/LatoRegular/LatoRegular.ttf") format("truetype");
	font-style: normal;
	font-weight: normal;
		font-display: fallback;
}

@font-face {
	font-family: "LatoBlack";
	src: url("../fonts/LatoBlack/lato-black.eot?#iefix")format("embedded-opentype"),
		url("../fonts/LatoBlack/lato-black.woff") format("woff"),
		url("../fonts/LatoBlack/lato-black.woff2") format("woff2"),
		url("../fonts/LatoBlack/lato-black.ttf") format("truetype");
	font-style: normal;
	font-weight: 800;
	font-display: swap;
}

